import React, { useEffect, useState } from "react";

import "./App.scss";

function App() {
  const countryUrl = "https://coronavirus-19-api.herokuapp.com/countries";
  const [cordata, setCorData] = useState([]);
  useEffect(() => {
    const fetchData = async () => {
      const response = await (await fetch(countryUrl)).json();
      setCorData(response);
      console.log(response);
    };
    fetchData();
  }, []);
  return (
    <div className="limiter">
      <div className="container-table100">
        <div className="wrap-table100">
          {cordata && (
            <div className="table100">
              <table>
                <thead>
                  <tr className="table100-head">
                    <th className="country">Country</th>
                    <th>Cases</th>
                    <th className="country">TodayCases</th>
                    <th>Deaths</th>
                    <th className="country">TodayDeaths</th>
                    <th>Recovered</th>
                    <th className="country">Active</th>
                    <th>Critical</th>
                    <th className="country">CasesPerOneMillion</th>
                    <th>deathsPerOneMillion</th>
                    <th className="country">TotalTests</th>
                    <th>TestsPerOneMillion</th>
                  </tr>
                </thead>
                <tbody>
                  {cordata.map((data, i) => (
                    <tr key={data.country}>
                      <td className="country">{data.country}</td>
                      <td>{data.cases}</td>
                      <td className="country">{data.todayCases}</td>
                      <td>{data.deaths}</td>
                      <td className="country">{data.todayDeaths}</td>
                      <td>{data.recovered}</td>
                      <td className="country">{data.active}</td>
                      <td>{data.critical}</td>
                      <td className="country">{data.casesPerOneMillion}</td>
                      <td>{data.deathsPerOneMillion}</td>
                      <td className="country">{data.totalTests}</td>
                      <td>{data.testsPerOneMillion}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default App;
